"use client"
import React from 'react'
import { useState } from 'react'
import { useRouter } from 'next/router'
import Input from './Input'

export default function FormLogin() {
    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')
    const router = useRouter()

    async function handleLogin(e: React.FormEvent){
        e.preventDefault()

        const response = await fetch('http://127.0.0.1:8000/api/token/', {
            method: 'POST',
            headers: {'Content-Type': 'aplication/json'},
            body: JSON.stringify({ email, password })
        })

        if(response.ok) {
            const data = await response.json()
            localStorage.setItem('stark_token', data.acess),
            router.push('/')
        } else {
            alert('Email ou(e) Senha inv√°lidos.')
        }
    }

    return (
        <div className="w-full flex items-center justify-center">
            <form className='w-[35%] h-100 p-15 shadow-2xl/10 rounded-3xl bg-white border-2 border-[#294D61] flex flex-col items-center justify-center gap-5'>
            <div className="flex flex-col gap-3">
            <div className="w-full flex flex-col">
                <span className="font-sans my-1">Email:</span>
                <input
                onChange={(e) => setEmail(e.target.value)}
                placeholder='Digite seu email'
                name="text"
                type='email'
                className="px-3 w-full bg-[#dfd1d1] p-2 border border-white/10 rounded-md outline-none ring-2 ring-blue-500/0 focus:ring-blue-500"/>
            </div>
            <div className="w-full flex flex-col">
                <span className="font-sans my-1">Email:</span>
                <input
                onChange={(e) => setPassword(e.target.value)}
                placeholder='Digite seu email'
                name="text"
                type='email'
                className="px-3 w-full bg-[#dfd1d1] p-2 border border-white/10 rounded-md outline-none ring-2 ring-blue-500/0 focus:ring-blue-500"/>
            </div>
                {/*<Input label='Email' type='Email' placeholder='Digite seu email'/>*/}
                {/*<Input label='Senha' type='password' placeholder='Digite sua senha'/>*/}
            </div>
                <button type="submit" className='w-full bg-[#294D61] hover:bg-[#0C7075] duration-150 ease-in-out px-3 py-2 rounded-md text-white cursor-pointer border'>Entrar</button>
            </form>
        </div>
    )
}
